/****************************************************************************************/
/* Client Specific Styling for C9 Work Specific Theme
/****************************************************************************************/

@import "client_variables";
body {
    color: $gray-900;
    font-family: $font-family-base;
}

p {
    line-height: 2;
}

a {
    color: $dark;
}

body.page {
    background: rgb(0, 0, 0);
    background: linear-gradient( 180deg, rgba(0, 0, 0, 0.8673844537815126) 1%, rgba(0, 0, 0, 0.667) 5%, rgba(0, 0, 0, 0) 13%);
    background-attachment: fixed;
    background-repeat: no-repeat;
}

a:hover {
    color: $link-hover-color;
}

.c9 .wrapper {
    margin-top: -115px;
    margin-bottom: 0px;
}

.single-post .wrapper,
.archive .c9 .wrapper {
    margin-top: 0px;
}

@media only screen and (min-width: 768px) {
    .blog .c9 .wrapper,
    .archive .c9 .wrapper {
        margin-top: 30px;
    }
}

.c9-layout-column-wrap {
    display: -ms-grid;
}

.c9-grid .c9-layout-column-wrap {
    position: relative;
}

@media only screen and (max-width: 667px) {
    .single .entry-content {
        padding: 20px;
    }
    .page-search-results {
        margin-top: 135px;
    }
    .blog .site .wrapper {
        margin-top: 30px;
    }
}

@media only screen and (min-width: 1200px) {
    .navbar .container {
        width: 1340px;
        max-width: 1340px;
    }
    #left-sidebar,
    #right-sidebar {
        background-color: $light;
    }
}

/****************************************************************************************/
/* Client default colors, button colors + background colors */
/****************************************************************************************/
.has-color-orange-color {
    color: $primary !important;
}

.has-color-yellow-color {
    color: $secondary !important;
}

.has-color-white-color {
    color: $light !important;
}

.has-color-black-color {
    color: $dark !important;
}

.has-color-gray-color {
    color: $gray-900 !important;
}

.bg-primary,
.has-color-orange-background-color {
    background-color: $primary;
}

.bg-secondary,
.has-color-yellow-background-color {
    background-color: $secondary;
}

.has-color-gray-background-color {
    background-color: $gray-100;
}

.has-color-white-background-color {
    background-color: #fff;
}

.has-color-black-background-color {
    background-color: $dark;
}

.bg-success {
    background-color: $success;
}

.bg-info {
    background-color: $info;
}

.bg-warning {
    background-color: $warning;
}

.bg-danger {
    background-color: $danger;
}

.bg-dark {
    background-color: $dark;
}

/****************************************************************************************/
/* Client buttons */
/****************************************************************************************/
.c9 .entry-content button,
.editor-styles-wrapper .wp-block-search__button,
.editor-styles-wrapper .button-primary,
.editor-styles-wrapper .mce-content-body.button,
.editor-styles-wrapper .wp-block-button__link,
.editor-styles-wrapper .c9-cta .c9-cta-button .wp-block-button__link,
.editor-styles-wrapper .button-primary,
.editor-styles-wrapper .mce-content-body.button,
.entry-content .btn,
.entry-content .btn:visited,
.entry-content button,
.entry-content .button,
.entry-content input[type="button"],
.entry-content input[type="reset"],
.entry-content input[type="submit"],
.entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.entry-content .wp-block-button__link,
.wp-block-button:not(.is-style-outline) .wp-block-button__link.mce-content-body,
.wp-block-button__link,
