/****************************************************************************************/
/* tags */

.i-tag,
.tags-links,
.cat-links {
    letter-spacing: 0.01em;
    text-transform: uppercase;
    font-weight: normal;
    color: $link-hover-color;
    font-size: 0.8em;
}

.tags-links a,
.cat-links a {
    text-decoration: none;
    color: $link-color;
